.text-input {
  outline: none;
  width: 100%;
  height: 48px;
  border-radius: 7px;
  background-color: #ffffff;
  text-align: center;
  background-color: color(input-background-color);
  display: block;
  font-size: 15px;
  font-family: font(playregular);
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 11px;
  margin-bottom: 11px;
  padding: 0 11px;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: color(input-text);
  border: 1px solid transparent;
  transition: none;
  &:focus {
    border: 1px solid color(input-text);
  }
  &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: color(input-text);
    font-size: 14px;
    line-height: 14px;
    position: relative;
    top: 10px;
    left: 0;
    text-align: left;
    text-transform: none;
  }
  &::-moz-placeholder { /* Firefox 19+ */
    color: color(input-text);
    font-size: 14px;
    line-height: 14px;
    position: relative;
    top: 10px;
    left: 0;
    text-align: left;
    text-transform: none;
  }
  &:-ms-input-placeholder { /* IE 10+ */
    color: color(input-text);
    font-size: 14px;
    line-height: 14px;
    position: relative;
    top: 10px;
    left: 0;
    text-align: left;
    text-transform: none;
  }
  &:-moz-placeholder { /* Firefox 18- */
    color: color(input-text);
    font-size: 14px;
    line-height: 14px;
    position: relative;
    top: 10px;
    left: 0;
    text-align: left;
    text-transform: none;
  }
}
