*,*:before,*:after {-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;}
ul,li {list-style:none;}
a{background-color:transparent;text-decoration: none;}
a:active,a:hover{outline:0;text-decoration: none}
article,aside,details,figcaption,figure,footer,header,main,menu,nav,section,summary{display: block;}
audio,canvas,progress,video{display:inline-block;vertical-align:baseline}
audio:not([controls]){display:none;height:0}
abbr[title]{border-bottom:1px dotted}
[hidden],template{display:none}
b, strong {font-weight: bold;}
img {border: 0;max-width: 100%;}
optgroup{font-weight:700}
mark{background:#ff0;color:#000}
dfn{font-style:italic}
small{font-size:80%}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline}
sup{top:-.5em}
sub{bottom:-.25em}
table {border-collapse: collapse;border-spacing: 0;}
td,th {padding: 0;}
hr{box-sizing:content-box;display: block;height: 0;border: 0;border-bottom: 1px solid #b8b8b8;}
audio,canvas,video {display: inline-block;}
audio:not([controls]) {display: none;height: 0;}
[hidden] {display: none;}
svg:not(:root) {overflow: hidden;}
figure{margin:1em 40px}
fieldset {margin: 0 2px;padding: 0.35em 0.625em 0.75em;border: 1px solid #c0c0c0;}
legend {padding: 0;border: 0;}
pre{overflow:auto}
code,kbd,pre,samp{font-family:monospace,monospace;font-size:1em}
button,label{outline: none;cursor: pointer;}
code {font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',  monospace;}
ul,li,p{margin: 0; padding: 0;}
a,button,input,label{transition: $transition}
html, body {margin: 0;padding: 0;font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
