$desktop: 840px;
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}

$main-page-margin: 30px;

body, a, button, p {
  font-family: font(playregular);
}
h1,h2,h3,h4,h5,h6 {
  font-family: font(montserrat);
}

html {
  height: 100%;
}

body {
  background: color(default-grey) url(../images/image-background-a.jpg) no-repeat center center fixed;
  background-size: 177%;
  padding: $main-page-margin;
  background-position-y: top;
  &.calculation {
    background-image: linear-gradient(150deg, color(background-gradiant-1) 2%, color(background-gradiant-2) 85%);
  }
}
a {
  color: color(primary);
  text-decoration: none;
  font-weight: bold;
}
p {
  color: color(body-text);
  text-decoration: none;
  font-weight: bold;
  font-size: 13px;
  margin-top: 7px;
  margin-bottom: 7px;
}
h1 {
  font-weight: 800;
  font-style: italic;
  color: color(body-text);
  text-shadow: 0 2px 8px rgba(4, 34, 99, 0.22);
  text-align: center;
  margin: 0;
  font-size: 33px;
}
h2 {
  font-weight: 600;
  font-style: italic;
  color: color(body-text);
  text-shadow: 0 2px 8px rgba(4, 34, 99, 0.22);
  text-align: center;
  margin: 0;
}
h3 {
  font-weight: 600;
  font-style: italic;
  color: color(body-text);
  text-shadow: 0 2px 8px rgba(4, 34, 99, 0.22);
  text-align: center;
  margin: 0;
  font-size: 14px;
}
h4 {
  font-weight: 200;
  font-style: italic;
  color: color(body-text);
  text-shadow: 0 2px 8px rgba(4, 34, 99, 0.22);
  text-align: center;
  margin: 0;
}

.container {
  margin: auto;
  max-width: 560px;
}

.quote-logo {
  margin-top: 40px;
  height: 70px;
  width: 100%;
  position: relative;
  z-index: 10;
}


.quote-logo-footer {
    margin: 3px auto 3px auto;
    height: 33px;
    width: 100%;
    position: absolute;
    z-index: 10;
    bottom: 7px;
    left: 0px;
    opacity: .77;
}

.font-test {
    font-family: montserrat;
    font-weight: 900;
    font-style: italic;
    color: color(primary);
}



.global-nav {
  display: flex;
  &__button {
    flex: auto;
    padding: 0 $main-page-margin / 2;
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
  }
}


.flex-container {
    padding: 0;
    margin: 0;
    display: flex;
    -webkit-flex-flow: row wrap;
    justify-content: space-around;
  }

.loading-spinner {
    width: 77px;
    margin-left: auto;
    margin-right: auto;
}
