body {
  &.location {
    width: 100%;
    height: 100%;
    padding: 0;
    overflow: hidden;
    #root {
      width: 100%;
      height: 100%;
    }
  }
}

.google-map {
  width: 100%;
  height: 100%;
  &--with-marker {
    .google-map__map {
      height: calc(100% - 90px);
    }
    .google-map__bottom {
      height: 120px;
    }
  }
  &__map {
    width: 100%;
    height: calc(100% - 50px);
    transition: $transition;
  }
  &__bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    border-radius: 21px 21px 0 0;
    box-shadow: 0 -4px 6px 0 rgba(0, 0, 0, 0.5);
    background-image: linear-gradient(109deg, #9fc522 2%, #739201 98%);
    transition: $transition;
    .container {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 100%;
      padding: 0 30px;
    }
    p {
      margin: 0;
    }
    .button-white {
      margin-top: 15px;
    }
  }
}
