$colors: (
    primary: #9FC522,
    primary-light: lighten(#9FC522, 40%),
    primary-dark: darken(#9FC522, 10%),

    accent: #4A90E2,
    accent-light: lighten(#4A90E2, 40%),
    accent-dark: darken(#4A90E2, 40%),


    body-text: #ffffff,
    button-text-white: #ffffff,

    background-gradiant-1: #9fc522,
    background-gradiant-2: #739201,
    border-color: rgba(255, 255, 255, 0.33),

    input-background-color: rgba(255, 255, 255, 0.23),
    input-text: #ffffff,

    button-white: #ffffff,
    button-green: #9FC522,

    grey-text: #6a6a6a,
    red-warning: #d0021b,

    default-grey: #333, //this is just for mocking up, not for final export

);

//Colors Mode
@function color($color-name) {
    @return map-get($colors, $color-name);
}
