.slider-wrapper {
  position: relative;
  height: 29px;
}

.slider-range {
  height: 24px;
  opacity: 0.39;
  border-radius: 33px;
  border: solid 4px #ffffff;
  background-color: rgba(255, 255, 255, 0.65);
}

.slider-button {
  font-size: 0;
  width: 55px;
  height: 29px;
  border-radius: 33px;
  border: solid 5px #ffffff;
  background-color: #ffffff;
  position: absolute;
  top: -3px;
  left: 33px;
}
