
//Standard Buttons -----------------------------
.button-white {
    //Edit this for height and LR padding
    $height: 40px;
    $padding-lr: 14px;
    //$margin-lr: 7px;

    width: 100%;
    text-align: center;
    border-radius: 7px;
    background-color: color(button-white);
    color: color(accent);
    display: block;
    cursor: pointer;
    font-size: 14px;
    font-family: 'Play';
    font-weight: bold;
    text-transform: uppercase;
    border: none;

    //do not edit
    line-height: $height;
    height: $height;
    padding-left: $padding-lr;
    padding-right: $padding-lr;
    //margin-right:$margin-lr;
    //margin-left:$margin-lr;
}


.button-white-text-green {
    color: color(primary);
}


