//Selection Buttons --------------------

.selection-button {

    //Edit this for height and LR padding
    $height: 40px;

    width: 100%;
    border-radius: 7px;
    background-color: color(input-background-color);
    display: block;
    color: color(button-text-white);
    text-align: left;
    padding-left: 10px;
    margin-bottom: 7px;

    //do not edit
    line-height: $height;
    height: $height;
}

.selection-button-focus {
    background-color: color(button-text-white);
    color: color(primary);
}

//End Selection Buttons -------------------





//Image Buttons-----------------------------

.buttons-icon {
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  position: relative;
  display: flex;
  flex-direction: column;
  background: none;
  &__img {
    position: relative;
    width: 85px;
    height: 85px;
    border-radius: 7.2px;
    background-color: color(input-background-color);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 77%;
    display: inline-block;
    transition: $transition;
    &--sun {
      background-image: url(../icons/icon-sun.svg);
    }
    &--tree {
      background-image: url(../icons/icon-tree-sun.svg);
    }
    &--shady {
      background-image: url(../icons/icon-shady.svg);
    }
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 1px solid transparent;
      border-radius: 7.2px;
      transition: $transition;
    }
    &--selected {
      background-color: rgba(255, 255, 255, 0.5);
      &:before {
        border: solid 3px color(button-white);
      }
    }
  }
  &__text {
    margin-top: 7px;
    font-weight: bold;
    text-align: center;
    width: 100%;
    color: color(button-white);
    font-size: 13px;
  }
}

//End Image Buttons
