//Calculating Quote Section
section#panel-overlay {
  padding-bottom: 100px;
  h1 {
    margin-top: 45px;
    font-size: 19.4px;
    text-align: left !important;
  }
  h2 {
    font-size: 13px;
    font-weight: 800;
    text-align: left !important;
  }
}

div#calculation-section {
  width: 100%;
  height: 157px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

div#calculation-line-wrapper {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  //background-color: black;
  //height: 10px;
  padding-right: 33px;
  padding-left: 33px;

  div#calculation-section-line {
    width: 100%;
    border-bottom: solid 1px color(border-color);
    //margin-top: 5px;
  }
}

div#calculation-text {
  font-family: 'Montserrat';
  font-size: 16px;
  font-weight: 800;
  font-stretch: normal;
  font-style: italic;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: color(body-text);
  margin: 33px 3px 7px 3px;
}

div#section-wrapper {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

div#section-icon {
  width: 121px;
  float: left;
  margin-right: 15px;
  position: relative;

  #icon-properties {
    width:118px;
  }

  #unlock-icon {
    position: absolute;
    top: 49px;
    left: -11px;
  }
}
