body {
  &.solar-page {
    width: 100%;
    height: 100%;
    padding: 0;
    overflow: hidden;
    #root {
      width: 100%;
      height: 100%;
    }
  }
}

.solar {
  width: 100%;
  height: 100%;
  &-map {
    width: 100%;
    height: calc(40% + 50px);
  }
  &-panel {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60%;
    border-radius: 21px 21px 0 0;
    box-shadow: 0 -4px 6px 0 rgba(0, 0, 0, 0.5);
    background-image: linear-gradient(109deg, #9fc522 2%, #739201 98%);
    transition: $transition;
    padding: 15px;
    &__circles {
      display: flex;
      height: calc(100% - 120px);
      padding-bottom: 10px;
      &-left {
        flex: 0 0 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        @media screen and (max-width: 480px) {
          flex: 0 0 55%;
        }
      }
      &-right {
        flex: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    &__slider {
      height: 65px;
      p {
        padding: 10px 0;
        margin: 0;
        text-align: center;
      }
    }
  }
}

.savings {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    left: -5px;
    top: -5px;
    width: calc(100% + 10px);
    height: calc(100% + 10px);
    border-radius: 100%;
    border: 5px solid rgba(255,255,255, 0.5);
  }
  &-circle.CircularProgressbar {
    //width: 200px;
    //height: 200px;
  }
}

.co2 {
  margin-bottom: 10px;
  &-circle {
    position: relative;
    &:before {
      content: "";
      position: absolute;
      left: -5px;
      top: -5px;
      width: calc(100% + 10px);
      height: calc(100% + 10px);
      border-radius: 100%;
      border: 3px solid rgba(255,255,255, 0.5);
    }
    &__svg.CircularProgressbar {
      width: 100px;
      height: 100px;
    }
  }
  [data-test-id="CircularProgressbarWithChildren__children"] {
    margin: 0 !important;
    left: 0;
    top: 0;
  }
}

.kwh {
  &-circle {
    width: 100px;
    height: 100px;
  }
  [data-test-id="CircularProgressbarWithChildren__children"] {
    margin: 0 !important;
    left: 0;
    top: 0;
  }
}
